import React, { useEffect, useRef, useState } from "react";
import { makeStyles, useTheme } from "@material-ui/core";
import { Canvas, extend, useThree } from '@react-three/fiber'
import { Stats, OrbitControls, Splat } from '@react-three/drei'
import { Camera3D } from "./Camera3D";
import SphericalUI from "./SphericalUI";

import FilipeGS from "../../assets/models/filipe_transparency-corrected.splat";
import OrbitControlsUIDark from "../../assets/images/OrbitControl_Arrows_DarkMode.png";
import OrbitControlsUILight from "../../assets/images/OrbitControl_Arrows_LightMode.png";

// import { LumaSplatsThree, LumaSplatsSemantics } from "@lumaai/luma-web";
// extend( { LumaSplats: LumaSplatsThree } ); // Make LumaSplatsThree available to R3F

const ProfilePicture3D = () => {
    const classes = useStyles();
    //const theme = useTheme();

    const canvasWrapperRef = useRef();
    const [showUIDelayed, setShowUIDelayed] = useState(false);
    const [triggerShowUI, setTriggerShowUI] = useState(false);
    const [triggerDisableUI, setTriggerDisableUI] = useState(false);
    const timerRef = useRef(null);

    let settings = {};
    settings.camera = {
        fov: 60,
        zoom: 1.2,
    }
    settings.ui = {
        loadDelay: 3000,
        defaultDuration: 2000,
        imageDark: OrbitControlsUIDark,
        imageLight: OrbitControlsUILight,
        opacity: 0.75,
        spherePos: [0, 0, 0],
        sphereRot: [0, -Math.PI / 6, 0],
        sphereRad: 1.8,
    }
    settings.splat = {
        src: FilipeGS,
        scale: 16,
        position: [0, -2.4, 0.15],
        rotation: [0, 0, 0], // [0, -Math.PI / 8, 0],
    }
    settings.light = {
        ambient: 0.5,
        point: [-10, -10, -10],
        spotPos: [10, 10, 10],
        spotAngle: 0.15,
        spotPenumbra: 1,
    }

    // Delay UI mount to ensure rest of scene is set
    useEffect(() => {
        const timeout = setTimeout(() => {
            setShowUIDelayed(true);
        }, 500);

        return () => clearTimeout(timeout);
    }, []);

    useEffect(() => {

        // Fade out UI after some time in view
        const observer = new IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting) {
                    const timeout = setTimeout(() => {
                        setShowUIDelayed(true);
                        setTriggerShowUI(true);
                        clearTimeout(timerRef.current);
                        timerRef.current = setTimeout(() => {
                            setTriggerShowUI(false);
                        }, settings.ui.defaultDuration);
                    }, settings.ui.loadDelay);
                }
            },
            { threshold: 0.1 }
        )

        // Also fade out on mouse hover
        const handleMouseEnter = () => {
            //console.log('Mouse entered canvas');
            setTriggerShowUI(true);
            clearTimeout(timerRef.current);
            timerRef.current = setTimeout(() => {
                setTriggerShowUI(false);
            }, settings.ui.defaultDuration);
        }

        // Ensure UI doesn't appear again once it has been interacted with 
        const handleMousePointerDown = () => {
            //console.log('Mouse clicked canvas');
            clearTimeout(timerRef.current);
            setTriggerShowUI(false);
            setTriggerDisableUI(true);
            observer.unobserve(canvasWrapperRef.current);
            canvasWrapperRef.current.removeEventListener('mouseenter', handleMouseEnter);
        }
        
        // Initiate observer and event listeners
        if (canvasWrapperRef.current) {
            observer.observe(canvasWrapperRef.current);
            canvasWrapperRef.current.addEventListener('mouseenter', handleMouseEnter);
            canvasWrapperRef.current.addEventListener('pointerdown', handleMousePointerDown);
        }
        
        // Cleanup function to remove the observer and event listeners
        return () => {
            if (canvasWrapperRef.current) {
                observer.unobserve(canvasWrapperRef.current);
                canvasWrapperRef.current.removeEventListener('mouseenter', handleMouseEnter);
            }
            clearTimeout(timerRef.current);
        }
    }, []);

    return (
        <div className={classes.canvasWrapper} ref={canvasWrapperRef}>
            <Canvas className={classes.ProfileImg}>
                <Camera3D fov={settings.camera.fov} zoom={settings.camera.zoom}/>

                {/* Interaction UI */}
                {showUIDelayed && <SphericalUI triggerShowUI={triggerShowUI} triggerDisableUI={triggerDisableUI} UIImageDark={settings.ui.imageDark} UIImageLight={settings.ui.imageLight} opacityNormalizer={settings.ui.opacity} spherePos={settings.ui.spherePos} sphereRot={settings.ui.sphereRot} sphereRad={settings.ui.sphereRad} />}
                
                {/* DREI Splats (https://drei.docs.pmnd.rs/abstractions/splat) */} 
                {/*<Splat src="https://huggingface.co/cakewalk/splat-data/resolve/main/nike.splat" />*/}
                <Splat
                    src={settings.splat.src} 
                    scale={settings.splat.scale} 
                    position={settings.splat.position}
                    rotation={settings.splat.rotation} // Rotate to face the camera
                />
                
                {/* LumaLabs Splats (https://lumalabs.ai/luma-web-library) */} 
                {/*<lumaSplats
                    semanticsMask={LumaSplatsSemantics.FOREGROUND}
                    source='https://lumalabs.ai/capture/7569fdd1-09e3-43f2-85c7-181b613c46eb'
                    position={[0, 0, 0]}
                    scale={3}
                />*/}

                {/* Lighting */} 
                <ambientLight intensity={settings.light.ambient} />
                <spotLight position={settings.light.spotPos} angle={settings.light.spotAngle} penumbra={settings.light.spotPenumbra} />
                <pointLight position={settings.light.point} />
                
                {/* Orbit Controls (https://sbcode.net/react-three-fiber/orbit-controls/) */} 
                <OrbitControls 
                    enablePan={false} 
                    enableZoom={false}
                    minPolarAngle={Math.PI / 2.5}
                    maxPolarAngle={Math.PI / 1.7}
                    minAzimuthAngle={Math.PI / 3}
                    maxAzimuthAngle={1.8 * Math.PI / 3}
                /> 

                {/* Canvas Statistics */} 
                {/*<Stats />*/} 

            </Canvas>
            <div className={classes.canvasOverlay}></div>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    canvasWrapper: {
        position: "relative",
        height:"70vh",
        [theme.breakpoints.down("sm")]: {
            height:"50vh",
            marginBottom: "5%",
        },
        [theme.breakpoints.down("xs")]: {
            height:"40vh",
            marginBottom: "10%",
        },
    },
    canvasOverlay: {
        position: "absolute",
        top: 0,
        left: 0,
        width: "101%",
        height: "101%",
        pointerEvents: "none", /* Ensure the overlay doesn't block interactions with the canvas */
        boxShadow: `0 0 60px 80px ${theme.palette.background.default} inset`,
        borderRadius: "50%",
        [theme.breakpoints.down("md")]: {
            boxShadow: `0 0 50px 60px ${theme.palette.background.default} inset`,
        },
        [theme.breakpoints.down("sm")]: {
            boxShadow: `0 0 50px 70px ${theme.palette.background.default} inset`,
        },
    },
    ProfileImg: {
        objectFit:'cover',
        objectPosition: "0 0px",
        display: "block",
        width: "100%",
        height: "100%",
        borderRadius: "50%",
    },
}));

export default ProfilePicture3D;
