import { useRef, useEffect } from 'react';
import { useThree, useFrame } from '@react-three/fiber';
import { RGBELoader } from 'three-stdlib'
import { PMREMGenerator, FloatType } from 'three'
import { calculateScrollOffset } from '../CustomMath';

const ReflectionHDR = ({ url, scrollOffsetMultiplier }) => {
    const { gl, scene } = useThree();
        const canvasRef = useRef();
        const envMapRef = useRef(null);
        let scrollOffset = 0;
      
        useEffect(() => {
            canvasRef.current = gl.domElement;
    
            const pmremGenerator = new PMREMGenerator(gl)
            pmremGenerator.compileEquirectangularShader()
        
            new RGBELoader()
                .setDataType(FloatType)
                .load(url, (texture) => {
                    const envMap = pmremGenerator.fromEquirectangular(texture).texture
                    scene.environment = envMap
                    envMapRef.current = envMap
                    texture.dispose()
                    pmremGenerator.dispose()
                })
        }, [gl, scene, url]);
    
        
        useFrame(() => {
            if (canvasRef.current) {
                scrollOffset = calculateScrollOffset(canvasRef.current.getBoundingClientRect().top + window.scrollY + canvasRef.current.offsetTop, -200);
    
                if(scrollOffset < 0) {
                    return;
                }
                
                // Normalize scrollOffset
                const fadeFactor = Math.max(0, Math.min(1, 1 - scrollOffset * scrollOffsetMultiplier));
    
                if(fadeFactor === 0) {
                    return;
                }
                
                // Traverse all meshes and set envMapIntensity
                scene.traverse((child) => {
                    if (child.isMesh && child.material) {
                        const materials = Array.isArray(child.material) ? child.material : [child.material];
                    
                        materials.forEach((mat) => {
                            // Only update if it’s a standard or physical material
                            if ((mat.isMeshStandardMaterial || mat.isMeshPhysicalMaterial) && mat.envMapIntensity !== undefined) {
                                mat.envMapIntensity = fadeFactor;
                                mat.envMap = scene.environment;
                                mat.needsUpdate = true;
                                //console.log(scrollOffset, fadeFactor, mat.envMapIntensity);
                            }
                        })
                    }
                });
            }
        });
      
        return null;
};

export default ReflectionHDR;