import React, { useContext, useRef, useEffect, useState } from "react";
import { makeStyles, useTheme, useMediaQuery } from "@material-ui/core";
import ThemeContext from "../../contexts/themeContext";

const swapImageSource = (targetID, newSource) => {
    if(newSource === "") return;
    //console.log(targetID, newSource);
    document.getElementById(targetID).src = newSource;
}

// Used to check if a Card is in viewport (i.e. is the most visible card on the screen)
const useIsInViewport = (id, ref, frontImage, frontAnimation) => {
    const [isIntersecting, setIsIntersecting] = useState(false);
    const isTouchscreen = useMediaQuery('(hover: none)');

    useEffect(() => {
        const observer = new IntersectionObserver(
            ([entry]) => {
                setIsIntersecting(entry.isIntersecting);

                // Make sure card image swap is also triggered on touchscreens
                if(isTouchscreen) {
                    if(entry.isIntersecting) {
                        swapImageSource(`${id}-cardImg`, frontAnimation);
                    } else {
                        swapImageSource(`${id}-cardImg`, frontImage);
                    }
                }
            },
            { threshold: 1.0 } // Adjust threshold as needed
        );

        if (ref.current) {
            observer.observe(ref.current);
            //console.log('is in viewport');
        }

        return () => {
            if (ref.current) {
                observer.unobserve(ref.current);
                //console.log('is NOT in viewport');
            }
        };
    }, [id, ref, frontImage, frontAnimation]);

    return isIntersecting;
};

const Card = ({ id, page, title, date, frontImage, frontAnimation, frontImageAlt, cardCoverage }) => {

    const hexToRgba = (hex, opacity) => {
        const bigint = parseInt(hex.slice(1), 16);
        const r = (bigint >> 16) & 255;
        const g = (bigint >> 8) & 255;
        const b = bigint & 255;
        return `rgba(${r}, ${g}, ${b}, ${opacity})`;
    };

    const ref = useRef(null);
    const theme = useTheme();
    const classes = useStyles(hexToRgba);
    const { isDarkMode } = useContext(ThemeContext);
    const isTouchscreen = useMediaQuery('(hover: none)');
    const isInViewport = useIsInViewport(id, ref, frontImage, frontAnimation);

    return (
        <a href={page} id={id} ref={ref}
            className={`${classes.cardRoot} ${isDarkMode ? classes.cardRootDark : classes.cardRootLight} ${classes[cardCoverage===33?"card33":cardCoverage===66?"card66":"card100"]} ${isTouchscreen && isInViewport ? (isDarkMode ? classes.cardInViewDark : classes.cardInViewLight) : ''}`}
            onMouseEnter={() => swapImageSource(`${id}-cardImg`, frontAnimation)} onMouseLeave={() => swapImageSource(`${id}-cardImg`, frontImage)}>
            <div className={classes.cardImgContainer}>
                <img className={`${classes.cardImg} ${classes[cardCoverage===33?"card1x1":cardCoverage===66?"card2x1":"card3x1"]}`} id={`${id}-cardImg`} src={frontImage} alt={frontImageAlt}></img>
            </div>
            <div className={`${classes.cardInfoContainer}`}>
                <h4><b>{title}</b></h4> 
                <p>{date}</p> 
            </div>
        </a>
    );
};

const useStyles = makeStyles((theme) => ({
    cardRoot: {
        zIndex: 1,
    },

    cardRootDark: {
        textDecoration: "none",
        transition: "0.5s",
        backgroundColor: theme.palette.background.default,
        color: theme.palette.text.primary,
        border: `2px solid ${theme.palette.background.default}`,
        "&:hover": {
            backgroundColor: theme.palette.primary.main,
            color: theme.palette.background.default,
        },
    },

    cardRootLight: (hexToRgba) => ({
        textDecoration: "none",
        transition: "0.5s",
        backgroundColor: theme.palette.background.default,
        color: theme.palette.text.primary,
        border: `2px solid ${theme.palette.text.primary}`,
        "&:hover": {
            backgroundColor: hexToRgba(theme.palette.primary.main, 0.1),
            //color: theme.palette.primary.main,
            border: `2px solid ${theme.palette.primary.main}`,
        },
    }),

    cardInViewDark: {
        backgroundColor: theme.palette.primary.main,
        color: theme.palette.background.default,
    },

    cardInViewLight: (hexToRgba) => ({
        backgroundColor: hexToRgba(theme.palette.primary.main, 0.1),
        //color: theme.palette.primary.main,
        border: `2px solid ${theme.palette.primary.main}`,
    }),

    card33: {
        flex: "1 1 33.33%",
        maxWidth: "33.33%",
        [theme.breakpoints.down("sm")]: {
            flex: "1 1 50%",
            maxWidth: "50%",
        },
        [theme.breakpoints.down("xs")]: {
            flex: "1 1 100%",
            maxWidth: "100%",
        }
    },

    card66: {
        flex: "1 1 66.66%",
        [theme.breakpoints.down("sm")]: {
            flex: "1 1 100%",
        }
    },

    card100: {
        flex: "1 1 100%",
    },

    cardInfoContainer: {
        textAlign: "left",
        marginLeft: "auto",
        marginRight: "auto",
        width: "100%",
        "& h4": {
            fontSize: "18px",
            margin: "6px 0px 0px 0px",
        },
        "& p": {
            fontSize: "14px",
            margin: "0px 0px 8px 0px",
        },
        "& h4, & p": {
            padding: "0px 5px 0px 5px",
        },
    },

    cardImgContainer: {
        display: "block",
        marginLeft: "0px",
        marginRight: "0px",
        marginBottom: "0px",
        width: "100%",
        overflow: "hidden",
        objectFit: "cover",
    },

    cardImg: {
        padding: "5px 5px 0px 5px",
        width: "100%",
        height: "auto",
        objectFit: "cover",
    },

    card1x1: {
        aspectRatio: "1 / 1",
        [theme.breakpoints.down("xs")]: {
            aspectRatio: "1 / 1",
        },
    },

    card2x1: {
        aspectRatio: "2 / 1",
        [theme.breakpoints.down("xs")]: {
            aspectRatio: "1 / 1",
        },
    },

    card3x1: {
        aspectRatio: "3 / 1",
        [theme.breakpoints.down("sm")]: {
            aspectRatio: "2 / 1",
        },
        [theme.breakpoints.down("xs")]: {
            aspectRatio: "1 / 1",
        },
    },
}));

export default Card;
