import React, { forwardRef, useEffect, useContext, useState } from "react";
import loaderContext from "../../contexts/loaderContext";
import { OBJLoader, GLTFLoader } from "three-stdlib";
import { useLoader, useFrame } from "@react-three/fiber";

const Model3D = forwardRef(({ path, material, position, rotation, scale, canRotate }, ref) => {

    const { isLoading } = useContext(loaderContext);

    // Choose the loader based on file extension
    const isGLB = path.toLowerCase().endsWith(".glb") || path.toLowerCase().endsWith(".gltf");
    const loadedData = useLoader(isGLB ? GLTFLoader : OBJLoader, path);

    // If loading a glb, we need to extract the scene
    const obj = isGLB ? loadedData.scene : loadedData;

    // Drag-related state
    const [isDragging, setIsDragging] = useState(false);
    const [lastX, setLastX] = useState(null);

    useEffect(() => {
        if (!isLoading && obj) {
            // Traverse the object tree and apply material and shadow properties to meshes
            obj.traverse((child) => {
                if (child.isMesh) {
                    if (material) {
                        child.material = material;
                    }
                    child.castShadow = true;
                    child.receiveShadow = true;
                }
            });
        }
    }, [isLoading, material, obj]);

    // Drag rotation logic
    const handlePointerDown = (e) => {
        e.stopPropagation();
        setIsDragging(true);
        setLastX(e.clientX);
    };

    const handlePointerUp = () => {
        setIsDragging(false);
        setLastX(null);
    };

    const handlePointerMove = (e) => {
        if (!isDragging || lastX === null) return;
        const deltaX = e.clientX - lastX;
        if (ref.current) {
            ref.current.rotation.y += deltaX * 0.01;
        }
        setLastX(e.clientX);
    };

    useFrame(() => {
        // Optional: implement momentum or easing here
    });

    return (
        <mesh>
            {obj && 
            <primitive
                ref={ref}
                object={obj}
                scale={scale}
                position={position}
                rotation={rotation}
                castShadow
                receiveShadow
                onPointerDown={canRotate? handlePointerDown : null}
                onPointerUp={canRotate? handlePointerUp : null}
                onPointerOut={canRotate? handlePointerUp : null}
                onPointerMove={canRotate? handlePointerMove : null}
            />}
        </mesh>
    );
});

export default React.memo(Model3D);