import React, { useState, useEffect } from "react";
// import { AnimatePresence, AnimateSharedLayout } from "framer-motion";
import {
  Grid,
  makeStyles,
  Button,
  Box,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import Card from "./Card";
import { immersiveProjectList, filmProjectList, designProjectList } from "../../data";
import { useTranslation } from "react-i18next";

const Gallery = ({type}) => {
  const classes = useStyles();
  const { t } = useTranslation();
  //const [selectedId, setSelectedId] = useState(null);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const [loadedProjects, setLoadedProjects] = useState([]);
  const [pageNo, setPageNo] = useState(0);
  const [pageSize, setPageSize] = useState(6);

  //const getSelected = (id) => newProjectList.find((elem) => elem.id === id);

  function loadProjects() {
    const startIndex = pageNo * pageSize;
    const endIndex = startIndex + pageSize;
    let newProjects = (type==="immersive" ? immersiveProjectList : type==="film" ? filmProjectList : designProjectList).filter((value, index) => {
      return index >= startIndex && index < endIndex;
    });
    //console.log('newProjects: ', newProjects);
    setLoadedProjects((prevProjects) => [...prevProjects, ...newProjects]);
    setPageNo((prevPageNo) => prevPageNo + 1);
  }

  useEffect(() => {
    if (isMobile) setPageSize(3);
    loadProjects();
  }, []);

  //console.log('loadedProjects after pageNo: ', pageNo,' pageSize: ', pageSize);
  //console.log(loadedProjects);
  return (
    <>
      <Grid container className={classes.mainContainer}>
        {loadedProjects.map((item, k) => (
            <Card
                id={item.id}
                page={item.page}
                title={item.title}
                date={item.date}
                frontImage={item.frontImage}
                frontAnimation={item.frontAnimation}
                frontImageAlt={item.frontImageAlt}
                cardCoverage={item.cardCoverage}
                initial={{ opacity: 0, y: -50 }}
                animate={{ opacity: 1, y: 0 }}
            />
        ))}
      </Grid>
      {loadedProjects.length < (type==="immersive" ? immersiveProjectList : type==="film" ? filmProjectList : designProjectList).length && (
        <Box display="flex" justifyContent="center" mt={2}>
          <Button
            className={classes.loadBtn}
            onClick={loadProjects}
            variant="contained"
            color="primary"
          >
            {t("project_load_btn")}
          </Button>
        </Box>
      )}
    </>
  );
};

const useStyles = makeStyles((theme) => ({
    mainContainer: {
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "left",
    },
    loadBtn: {
        width: "200px",
    },
}));

export default Gallery;
