import React, { useEffect, useRef, useState, useContext, Suspense } from 'react'
import ThemeContext from '../../contexts/themeContext';
import { useThree, useLoader, useFrame } from '@react-three/fiber'
import { TextureLoader } from 'three'

const SphericalUI = ({triggerShowUI, triggerDisableUI, UIImageDark, UIImageLight, opacityNormalizer, spherePos, sphereRot, sphereRad}) => {
    const { isDarkMode } = useContext(ThemeContext);

    const materialRef = useRef();
    const texture = useLoader(TextureLoader, isDarkMode ? UIImageDark : UIImageLight);
    const side = useThree().FrontSide;

    const [shouldRenderUI, setShouldRenderUI] = useState(false);
    const [opacity, setOpacity] = useState(0);
    const [fadeState, setFadeState] = useState('idle'); // 'idle' | 'fadingIn' | 'visible' | 'fadingOut'
    const holdTimer = useRef(null);

    let holdTime = 1000; // Adjust this value to control the hold time before fading in/out
    let fadeSpeed = 0.75; // Adjust this value to control the speed of the fade in/out
    
    useEffect(() => {
        if (triggerShowUI && fadeState === 'idle') {
            setShouldRenderUI(true);
            setFadeState('fadingIn');
            //console.log('fadingIn');
            setOpacity(0);
            clearTimeout(holdTimer.current);
        }
        if(triggerDisableUI && (fadeState === 'visible' || fadeState === 'fadingIn')) {
            //console.log('Spherical UI was informed that mouse clicked canvas');
            setFadeState('fadingOut');
            //console.log('fadingOut');
            clearTimeout(holdTimer.current);
        }
    }, [triggerShowUI]);

    useFrame((_, delta) => {
        if (!shouldRenderUI) return;

        if (fadeState === 'fadingIn') {
            //console.log('fadingIn...');
            setOpacity(prev => {
                const next = prev + delta * fadeSpeed;
                if (next >= 1) {
                    setFadeState('visible');
                    holdTimer.current = setTimeout(() => {
                        setFadeState('fadingOut');
                        //console.log('fadingOut');
                    }, holdTime);
                    return 1;
                }
                return next;
            });
        }
        
          if (fadeState === 'fadingOut') {
            //console.log('fadingOut...');
            setOpacity(prev => {
                const next = prev - delta * fadeSpeed;
                if (next <= 0) {
                    setShouldRenderUI(false);
                    setFadeState('idle');
                    //console.log('idle');
                    return 0;
                }
                return next;
            });
        }

        if (materialRef.current) {
            materialRef.current.opacity = opacity * opacityNormalizer; // Adjust opacity based on the state
        }
    });

    return (
        <Suspense fallback={null}>
            {shouldRenderUI &&
            <mesh position={spherePos} rotation={sphereRot} renderOrder={999}>
                <sphereGeometry args={[sphereRad, 64, 64]} />
                <meshBasicMaterial
                    map={texture}
                    transparent={true}
                    //color={isDarkMode ? 'white' : 'black'}
                    side={side} // set to 2 to enable DoubleSide, so it's visible inside and out
                    depthWrite={false} // Disabled because, even though a material is transparent, by default it still writes to the depth buffer, which can cause objects behind it to disappear or clip when viewed at certain angles
                    depthTest={false}
                    opacity={opacity * opacityNormalizer} 
                    alphaTest={0.1} // Optional: helps discard very transparent pixels
                />
            </mesh>}
        </Suspense>
    )
}

export default SphericalUI;