import React, { useRef, useEffect, useContext, useMemo, useState, Suspense } from 'react';
import { useThree, useFrame } from '@react-three/fiber';
import { Vector3, Color } from 'three';
import { EffectComposer, Vignette, Bloom } from '@react-three/postprocessing'
import { LensFlare } from "@andersonmancini/lens-flare";
import { useTheme, useMediaQuery } from "@material-ui/core";
import ThemeContext from '../../contexts/themeContext';
import {calculateScrollOffset, normalize} from "../CustomMath";
import Model3D from "./Model3D";
import ReflectionHDR from './ReflectionHDR';
import studioHDR from "../../assets/hdri/studio.hdr";

const BLOOM_LAYER = 1;

const LightBulb3D = ({ path, position, rotation, scale }) => {
    const { gl, camera } = useThree();
    const canvasRef = useRef();
    const modelRef = useRef();
    const theme = useTheme();
    const { isDarkMode } = useContext(ThemeContext);
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
    const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));
    //console.log(isMobile, isTablet);

    const lensFlareColor = useMemo(() => new Color(56, 22, 11), []);
    const lensFlarePos = useMemo(() => 
        isTablet ? (new Vector3(position[0]+0.025, position[1]+0.2, 0)) : 
        ( isMobile ? (new Vector3(position[0]+0.005, position[1]+0.12, 0)) : (new Vector3(position[0]+0.01, position[1]+0.21, 0)) )
        , []);
    //console.log(lensFlarePos);

    let scrollOffset = 0;
    const smoothFactor = 0.1;

    const turnedOnRef = useRef(false);
    const [effectsEnabled, setEffectsEnabled] = useState(false); // trigger re-render for Bloom component


    useEffect(() => {
        canvasRef.current = gl.domElement;

        if (canvasRef.current) {

        }

    }, [gl]);

    // Make sure the camera sees the bloom layer too
    useEffect(() => {
        camera.layers.enable(BLOOM_LAYER);
    }, [camera]);

    useFrame(() => {
        if (modelRef.current && canvasRef.current) {
            scrollOffset = calculateScrollOffset(canvasRef.current.getBoundingClientRect().top + window.scrollY + canvasRef.current.offsetTop, -200);
            //console.log(scrollOffset);

            // Turn on/off bloom layer (i.e. light) based on scroll position

            const shouldTurnOn = scrollOffset > (isTablet ? 0.0 : ( isMobile ? 0.1 : 0.25 ));

            if (shouldTurnOn !== turnedOnRef.current) {
                turnedOnRef.current = shouldTurnOn;
                //console.log(`Light bulb turned ${turnedOnRef.current ? 'on' : 'off'}`);

                // Toggle bloom layer on model
                if (modelRef.current) {
                    if (shouldTurnOn) {
                        modelRef.current.layers.enable(BLOOM_LAYER);
                    } else {
                        modelRef.current.layers.disable(BLOOM_LAYER);
                    }
                }
        
                // Trigger re-render to update Bloom
                setEffectsEnabled(shouldTurnOn);
            }

            // Fade the canvas opacity

            const opacityZeroLowLimitOffset = isTablet ? -0.5 : ( isMobile ? -0.7 : -0.5 );
            const opacityZeroHighLimitOffset = 1.0;
            const opacityFadeInHighLimitOffset = 0.0;
            const opacityFadeOutLowLimitOffset = isTablet ? 0.6 : ( isMobile ? 0.6 : 0.8 );
            let opacity = 1;

            if (scrollOffset < opacityZeroLowLimitOffset) {
                opacity = 0;
            } else if (scrollOffset < opacityFadeInHighLimitOffset) {
                opacity = Math.min(1, normalize(scrollOffset, opacityZeroLowLimitOffset, opacityFadeInHighLimitOffset) * 5);
            } else if (scrollOffset > opacityZeroHighLimitOffset) {
                opacity = 0;
            } else if (scrollOffset > opacityFadeOutLowLimitOffset) {
                opacity = Math.max(0, 1-normalize(scrollOffset, opacityFadeOutLowLimitOffset, opacityZeroHighLimitOffset) * 5);
            }
            canvasRef.current.style.opacity = opacity;
            //console.log(scrollOffset, canvasRef.current.style.opacity);
        }
    });

    return (
        <Suspense fallback={ null }>

            <Model3D 
                ref={modelRef}
                path={path}
                material={null}
                position={position}
                rotation={rotation}
                scale={scale}
                canRotate={true}
            />
            
            <ReflectionHDR 
                url={studioHDR} 
                scrollOffsetMultiplier={0}
            />
            
            {/* Postprocessing */}
            <EffectComposer multisampling={8}>
                {/* 
                <Bloom
                    intensity={turnedOnRef.current ? 1.2 : 0}
                    luminanceThreshold={0.1}
                    luminanceSmoothing={0.9}
                    height={1024} // ← bump this up from default 300
                />
                */}
                <LensFlare 
                    position={lensFlarePos}
                    opacity={turnedOnRef.current ? (isDarkMode ? 0.2 : 0.1) : 0.0}
                    colorGain={lensFlareColor}
                    glareSize={0.1}
                    starPoints={1}
                    animated={false}
                    followMouse={false}
                    anamorphic={false}
                    flareSpeed={0.2}
                    flareShape={0.1}
                    flareSize={0.05}
                    secondaryGhosts={false}
                    starBurst={false}
                    haloScale={0.01}
                />
                {/* 
                <Vignette
                    eskil={false}        // Use the "fast" version of the vignette
                    offset={0.1}         // How far into the screen the vignette starts [0–1]
                    darkness={1.5}       // How dark the corners get
                />
                */}
            </EffectComposer>
        </Suspense>
    )
};

export default LightBulb3D;