import React, { useRef, useEffect, useContext, Suspense } from 'react';
import { useThree, useFrame } from '@react-three/fiber';
import { useTheme, useMediaQuery } from "@material-ui/core";
import ThemeContext from '../../contexts/themeContext';
import {calculateScrollOffset, lerp, normalize} from "../CustomMath";
import Model3D from "./Model3D";

const REDCamera3D = ({ path, position, rotation, scale, scrollRotationFactor }) => {
    const modelRef = useRef();
    const pointLightRef = useRef();
    const spotLightRef = useRef();
    const theme = useTheme();
    const { isDarkMode } = useContext(ThemeContext);
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

    let scrollOffset = 0;

    const { gl, scene } = useThree();
    const canvasRef = useRef();

    useEffect(() => {
        canvasRef.current = gl.domElement;

        if (spotLightRef.current && modelRef.current) {
            spotLightRef.current.target = modelRef.current;
            scene.add(modelRef.current); // required for it to work
        }
    }, [gl, scene]);

    useFrame(() => {
        if (modelRef.current && canvasRef.current) {
            scrollOffset = calculateScrollOffset(canvasRef.current.getBoundingClientRect().top + window.scrollY + canvasRef.current.offsetTop, -200);
            scrollOffset = (scrollOffset + 1) * 1.2;

            if(scrollOffset < 0) {
                return;
            }
            
            let xPositionValue = 0;
            let yPositionValue = 0;

            const initialYRotation = rotation[1]; // * (Math.PI / 180);
            let yRotationValue = 0;

            let smoothedScaleValue = scale;

            yPositionValue = position[1] - Math.pow(scrollOffset * 0.3, 2);

            if(isMobile) {
                xPositionValue = position[0];
            } else {
                if(scrollOffset > 1) {
                    xPositionValue = position[0];
                } else {
                    xPositionValue = position[0] -scrollOffset + 1;
                }
            }

            if(isMobile) {
                if(scrollOffset > 1) {
                    yRotationValue = 1 * scrollRotationFactor;
                }
                else if(scrollOffset < 0.25) {
                    yRotationValue = 0;
                }
                else {
                    yRotationValue = (scrollOffset-0.25) * 1.3 * scrollRotationFactor;
                }    
            } else {
                if(scrollOffset > 1.5) {
                    yRotationValue = 1 * scrollRotationFactor;
                    //xPositionValue = position[0];
                }
                else if(scrollOffset < 0.75) {
                    yRotationValue = 0;
                    //xPositionValue = 0.25;
                }
                else {
                    yRotationValue = (scrollOffset-0.75) * 1.35 * scrollRotationFactor;
                    //xPositionValue = position[0] + (1-scrollOffset)/2.5;
                }    
            }
                    

            if(isMobile) {
                //smoothedScaleValue = lerp(modelRef.current.scale.x, Math.max(scale, (1+scrollOffset) * Math.pow((scrollOffset),2) * scale * 1.3 * 2 ));
                smoothedScaleValue = lerp(modelRef.current.scale.x, Math.max(scale, (1+scrollOffset) * Math.pow((scrollOffset),4) * scale * 1.3 * 1.5 ));
            } else {
                smoothedScaleValue = lerp(modelRef.current.scale.x, Math.max(scale, (1+scrollOffset) * Math.pow((scrollOffset),4) * scale * 1.3 ));
            }

            // Position scroll animation
            modelRef.current.position.x == position[0] ? modelRef.current.position.x = xPositionValue : modelRef.current.position.x = lerp(modelRef.current.position.x, xPositionValue);
            modelRef.current.position.y == position[1] ? modelRef.current.position.y = yPositionValue : modelRef.current.position.y = lerp(modelRef.current.position.y, yPositionValue);
            
            // Rotation scroll animation
            modelRef.current.rotation.y = lerp(modelRef.current.rotation.y, initialYRotation + yRotationValue);

            // Scale scroll animation
            modelRef.current.scale.set(smoothedScaleValue, smoothedScaleValue, smoothedScaleValue);

            // Lights
            if(pointLightRef.current) {
                pointLightRef.current.position.x = lerp(pointLightRef.current.position.x, scrollOffset);
            }

            // Fade the canvas opacity

            const opacityFadeOutLowLimitOffset = 1.9;
            const opacityFadeOutHighLimitOffset = 2.4;

            if(scrollOffset > opacityFadeOutHighLimitOffset) {
                canvasRef.current.style.opacity = 0;
            } else if(scrollOffset > opacityFadeOutLowLimitOffset) {
                canvasRef.current.style.opacity = Math.max(0, 1 - normalize(scrollOffset, opacityFadeOutLowLimitOffset, opacityFadeOutHighLimitOffset) * 2);
            }
            
        }
    });

    return (
        <Suspense fallback={ null }>
            <Model3D ref={modelRef} path={path} material={null} position={position} rotation={rotation} scale={scale} canRotate={false}/>
            <pointLight ref={pointLightRef} position={[0, -0.1, 0.5]} intensity={isDarkMode ? 5 : 10} />
            <spotLight ref={spotLightRef} position={[-0.5, 0, -2]} intensity={isDarkMode ? 1 : 5}/>
        </Suspense>
    )
};

export default REDCamera3D;