import React from 'react';

import ArchangelMichaelStatue3D from "./ArchangelMichaelStatue3D";
import ArchangelMichael from "../../assets/models/Archangel Michael Statue.obj";

import AppleVisionPro3D from './AppleVisionPro3D';
import AppleVisionPro from "../../assets/models/Apple Vision Pro.glb";

import REDCamera3D from "./REDCamera3D";
import REDCamera from "../../assets/models/RED Camera.glb";

import LightBulb3D from "./LightBulb3D";
import LightBulb from "../../assets/models/LightBulb.glb";

import ProfilePicture3D from './ProfilePicture3D';

export const AssetType = Object.freeze({
    Home: 1,
    Immersive: 2,
    Film: 3,
    Design: 4
});

const Asset3D = ({ type, position, rotation, scale, scrollRotationFactor, material }) => {

    return (
        <>
            {type === AssetType.Home && <ArchangelMichaelStatue3D path={ArchangelMichael} material={material} position={position} rotation={rotation} scale={scale} scrollRotationFactor={scrollRotationFactor} />}
            {type === AssetType.Immersive && <AppleVisionPro3D path={AppleVisionPro} position={position} rotation={rotation} scale={scale} scrollRotationFactor={scrollRotationFactor} />}
            {type === AssetType.Film && <REDCamera3D path={REDCamera} position={position} rotation={rotation} scale={scale} scrollRotationFactor={scrollRotationFactor} />}
            {type === AssetType.Design && <LightBulb3D path={LightBulb} position={position} rotation={rotation} scale={scale} />}
            {type === AssetType.Profile && <ProfilePicture3D />}
        </>
    )
};

const MemoizedAsset3D = React.memo(Asset3D);

export { MemoizedAsset3D as Asset3D };